body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: rgb(12, 83, 33);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.color-selector {
  list-style-type: none;
}

.card {
  display: -webkit-flex; 
  -webkit-align-items: center; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 90px;
  height: 130px;
  border: 3px solid #ffffff;
  border-radius: 6px;
  padding: 5px;
  background: -webkit-gradient(linear, left top, left bottom, from(red), color-stop(yellow), color-stop(green), to(blue));
  background: -webkit-linear-gradient(red, yellow, green, blue);
  background: -o-linear-gradient(red, yellow, green, blue);
  background: linear-gradient(red, yellow, green, blue);
  -webkit-box-shadow: 3px 3px 3px rgb(43, 42, 42);
          box-shadow: 3px 3px 3px rgb(43, 42, 42);
  cursor: pointer;
}


.card--yellow {
  background: yellow;
}

.card--blue {
  background: blue;
}

.card--red {
  background: red;
}

.card--green {
  background: green;
}

.card-item {
  width: 70px;
  margin: 0 5px 0 5px;
  padding-top: 10px;
  height: 90px;
  border-top-left-radius: 40px;
  border-bottom-right-radius: 40px;
  background-color: white;
  font-size: 60px;
  color: rgb(19, 18, 18);
  text-align: center;
  text-shadow: 1px 1px rgb(61, 61, 61);
}

.table-card {
  width: 90px;
  height: 130px;
  border: 2px rgb(255, 255, 255) solid;
  border-radius: 6px;
}

.player-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  list-style-type: none;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.table-cards {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  list-style-type: none;
}

.backside-card {
  margin: 0;
  color: rgb(238, 230, 185);
  font-weight: bold;
  border-left-color: yellow;
  border-right-color: red;
  border-top-color: yellow;
  border-bottom-color: red;
}
.pl-index0, .pl-index1 {
  background-color: rgb(10, 163, 36);
}

.pl-index0 {
  -ms-flex-order: 1;
      order: 1;
}

.pl-index1 {
  -ms-flex-order: 3;
      order: 3;
}

.table {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-order: 2;
      order: 2;
  margin: 40px 40px 0 40px;
}

.color-selector {
  margin-left: 30px;
  padding-left: 0px;
  border: 5px solid white;
  border-bottom-left-radius: 10px; 
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  -webkit-box-shadow: 3px 3px 3px rgb(43, 42, 42);
          box-shadow: 3px 3px 3px rgb(43, 42, 42);
}

.color-selector li { 
  color: white;
  padding: 5px 10px;
  cursor: pointer;
  text-align: center;
}

h2 {
  color: white;
}

.player-title {
  color: white;
}

.player-title--active {
  color: red;
}


.deck-cards {
  margin: 0 40px;
}

.gameOver {
  height: 100%;
  width: 100%;
  padding-top: 200px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(6, 12, 8, 0.8);
  color: white;
  text-align: center;
  z-index: 100;
}


